@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Fonts */

/* #0099ff */

body {
    background-color: #92e0fb;
    font-family: 'Lobster', cursive;
}

p, h1, h2, h3, h4, h5 {
    color: #015c99;
    /* text-shadow: 1px 1px #000000; */
    font-family: 'Poppins', cursive;
}

h1 {
    font-family: 'Lobster', cursive;
    font-size: 40pt;
    text-shadow: 1px 1px #000000;
}

p {
    font-size: large;
}


i.fas,
i.fab {
    color: white;
    text-shadow: 1px 1px #000000;
}

.contentWrap {
    min-height: calc(100vh - 596px);
    margin-left: auto;
    margin-right: auto;
    border-radius: 25% 25% 25% 25% / 25% 25% 25% 25% ;
    background-color: rgba(222, 252, 253, 0.945);
    width: 80%;
    margin-top: 10vh;
    margin-bottom: 50vh;
};

.navbar {
    transition: all 0.4s;
    background-color: #0099ff;
    z-index: 100;
    position: relative;
    width: 100%;
    text-shadow: 1px 1px #000000;
}

.navbar .nav-link {
    color: rgb(255, 255, 255);
    font-size:1.75rem;
}

.navbar .nav-link:hover,
.navbar .navbar-brand:hover {
    color: rgb(255, 236, 66);
    text-decoration: none;
}

.navbar .navbar-brand {
    color: rgb(255, 255, 255);
    font-size: 2.5rem;
}

/* Change navbar styling on scroll */
.navbar.active {
    background: #fff;
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
}

.navbar.active .nav-link {
    color: #555;
}

.navbar.active .nav-link:hover {
    color: #555;
    text-decoration: none;
}

.navbar.active .navbar-brand {
    color: #555;
}

.dropdown-menu {
    background-color: #3bb1ff;
    border-width: 0;
    border-color: black;
}

.dropdown-item {
    color:white;
}

.navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgb(255, 255, 255)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

button {
    color: white;
}


@media (max-width: 330px) {
  .navbar .navbar-brand {
    font-size: 2rem;
  }
}

.customFooter {
    /* position: fixed; */
    left: 0;
    bottom: 0;
    width: 100%;
}

.footerFiller {
    width:100%;
    height: 120px;
    background-color: #0099ff;
    padding-bottom: 2vh;
}

.socialBox {
    border-radius: 100%;
    border-width: 20px;
    border-color: black;
    margin-bottom: 0;
    width: 90px;
    height: 90px;
    background-color: #92e0fb;
}

.socialBox:hover { 
    background-color: rgb(255, 245, 158);
}

.socialBox:hover i.fas,
.socialBox:hover i.fab { 
    color: #0099ff;
}

.socialBox a {
    border-radius: 100%;
    text-decoration: none;
}

@media (max-width: 576px) {
    .socialBox {
        width: 70px;
        height: 70px;
    }
}

#footer{ clear:both; }
